import React from 'react';
import MetaTags from 'react-meta-tags';

import I_S from '../Images/Image.jpg';


class Meta extends React.Component {

    Tags = () => {
        //console.log(window.location.href.split('/').pop());
        if (window.location.href.split('/').pop() === '') {
            return (
                <MetaTags>
                    <title>Home</title>
                    <meta name="description" content="Home - portfolio." />
                    {/*<meta property="og:title" content="Homepage" />*/}
                    <meta property="og:image" content={I_S} />
                </MetaTags>);
        }
        
        else if (window.location.href.split('/').pop() === 'apps') {
            return (
                <MetaTags>
                    <title>Apps</title>
                    <meta name="description" content="Apps - portfolio." />
                    {/*<meta property="og:title" content="Apps Page" />*/}
                    <meta property="og:image" content={I_S} />
                </MetaTags>);
        }
        else if (window.location.href.split('/').pop() === 'social-media') {
            return (
                <MetaTags>
                    <title>Social Media</title>
                    <meta name="description" content="Social Media Page - portfolio." />
                    {/* <meta property="og:title" content="Social Media" />*/}
                    <meta property="og:image" content={I_S} />
                </MetaTags>);
        } 
    }

    render() {
               return (
            <div className="wrapper">
                {this.Tags()}
                {/*<div className="content"> Some Content </div>*/}
            </div>
        );

    }
    
}

export default Meta;
